body {
    background-image: linear-gradient(
            0deg,
            rgba(255, 0, 242, 0.699),
            rgba(8, 12, 78, 0.808)
        ),
        linear-gradient(90deg, rgb(0, 216, 245), rgb(238, 255, 0));
    background-image: linear-gradient(
        0deg,
        rgba(13, 13, 27, 1),
        rgb(27, 27, 34)
    );
    background-size: 100%;
    min-height: 100vh;
}
a:focus {
    outline: none;
}
.navbar {
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);
    gap: 10px;
    padding: 10px 20px;
}
.navbar-logo {
    width: 84px;
    height: 84px;
    display: block;
}
.navbar-header {
    color: white;
    font-family: Reaver-SemiBold;
    font-size: 34px;
}
.heroes-list-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    padding: 0 20px;
    gap: 10px;
}
.heroes-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    border-width: 4px;
    border-style: solid;
    padding: 10px;
}
.heroes-list.str {
    border-image: linear-gradient(to bottom, #7c2611, #d3401c) 1 100;
    background-color: rgba(124, 38, 17, 0.2);
}

.heroes-list.agi {
    border-image: linear-gradient(to bottom, #0a6a10, #10a619) 1 100;
    background-color: rgba(10, 106, 16, 0.2);
}

.heroes-list.int {
    border-image: linear-gradient(to bottom, #1d517c, #328bd3) 1 100;
    background-color: rgba(29, 81, 124, 0.2);
}

.heroes-list.all {
    border-image: linear-gradient(to bottom, #4c1163, #8822b1) 1 100;
    background-color: rgba(76, 17, 99, 0.2);
}
.hero-button-container {
    outline: none;
    outline-width: 2px;
    display: inline-block;
}
.hero-button {
    height: 56px;
    width: 102px;
    border: 1px solid black;
    /* transition: all; */
    /* transition-duration: 0.15s; */
    width: 100px;
    outline: none;
    outline-width: 0;
    display: inline-block;
}
img::before {
    border: 2px solid red;
}
.hero-button:not(.not-active-search):hover {
    filter: brightness(1.2);
}
.hero-button:active {
    transform: scale(0.95);
}

.active-search {
    border: 1px solid rgb(0, 255, 234);

    animation-name: search-hightlight;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
}

@keyframes search-hightlight {
    0% {
        box-shadow: 0 0 2px 1px rgb(0, 255, 234);
    }
    50% {
        box-shadow: 0 0 6px 4px rgb(0, 255, 234);
    }
    100% {
        box-shadow: 0 0 2px 1px rgb(0, 255, 234);
    }
}

.not-active-search {
    filter: saturate(0) contrast(0.7) brightness(0.35);
}

.abilities-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    flex-direction: column;
    gap: 15px;
}
.ability-name {
    color: white;
    font-family: Reaver-SemiBold;
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 22px;
    /* letter-spacing: 1px; */
}
.ability-icon {
    width: 50px;
    height: 50px;
    box-shadow: 0 2px 4px 0px rgb(15, 15, 15);
}
.upgrade-basic-value-container {
    display: flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;
}
.upgrade-basic-value {
    color: white;
    font-family: Radiance-Bold;
    font-size: 20px;
}

.upgrade-max-count {
    background-color: rgba(0, 0, 0, 0.3);
    color: #fbc64b;
    font-family: Radiance-Bold;
    padding: 3px 13px;
    margin-left: auto;
    margin-right: 0;
    border: 1px solid #6f6f97;
    border-radius: px;
    width: 60px;
    text-align: center;
}

.ability-upgrades-container {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-top: 5px;
}
.ability-block {
    background: linear-gradient(0deg, #211f28, #363245);
    padding: 15px;
    box-shadow: 0 3px 6px 1px rgb(15, 15, 15);
}
.upgrade-block {
    background: rgba(0, 0, 0, 0.273);
    padding: 10px;
}
.linked-ability-image {
    width: 28px;
    height: 28px;
    box-shadow: 0 2px 4px 0px rgb(15, 15, 15);
}
.about-hero-container {
    display: block;
    max-width: 1250px;
    margin: auto;
    margin-bottom: 15px;
}
.about-hero-info {
    display: flex;
    align-items: center;
    color: white;
    font-family: Reaver-Bold;
    font-size: 24px;
    gap: 10px;
    /* width: max-content; */
    padding: 8px 15px;
    background-color: #363245;
    box-shadow: 0 3px 6px 1px rgb(15, 15, 15);
    margin: 15px 0;
}
.about-hero-icon {
    width: 108px;
    height: 61px;
    box-shadow: 0 2px 4px 0px rgb(15, 15, 15);
}
.has-linkeds .linked-upgrades-container {
    display: block;
}
.linked-ability:first-child {
    margin-top: 4px;
}
.linked-ability {
    color: white;
    font-family: Radiance-Regular;
    height: 28px;
    display: flex;
    flex-direction: row;
    gap: 6px;
    align-items: center;
}
.tree-icon {
    height: 100%;
    width: 28px;
    background-image: url("../../public/tree_default.png");
    background-size: 100%;
}
.linked-ability:last-child .tree-icon {
    background-image: url("../../public/tree_end.png");
}
.home-page {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.search-container {
    background-color: rgb(101, 101, 101);
    margin: 15px 0;
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: row;
    align-items: center;
}
.search-input {
    padding: 8px 14px;
    width: 500px;
    font-family: Radiance-Regular;
    font-size: 24px;
    background-color: transparent;
    border: none;
    color: rgb(199, 216, 255);
}
.reset-search {
    background-color: rgb(21, 15, 35);
    border: 2px solid rgb(51, 55, 90);
    color: white;
    font-family: Radiance-Regular;
    text-transform: uppercase;
    margin: 1px 8px 0 8px;
    font-size: 17px;
}
.reset-search:hover {
    filter: brightness(1.2);
}
.reset-search:active {
    filter: brightness(0.7);
}

.language-selectors {
    display: flex;
    gap: 12px;
    margin: auto;
    margin-right: 0;
}

.language-button {
    width: 35px;
    height: 35px;
    border: none;
    background-color: transparent;
    background-size: 100%;
    border-radius: 50%;
    border: 2px solid rgb(68, 68, 68);
    box-shadow: inset 0px 0px 4px 0px black;
    transition-property: all;
    transition-duration: 0.15s;
}
.language-button:hover {
    filter: brightness(1.2);
}
.language-button:active {
    transform: scale(0.95);
}
.language-button.eng {
    background-image: url("../../public/flag_eng.png");
}
.language-button.ru {
    background-image: url("../../public/flag_ru.png");
}
.language-button.eng:lang(en),
.language-button.ru:lang(ru) {
    border: 2px solid rgb(245, 147, 0);
    box-shadow: inset 0px 0px 4px 0px black,
        0px 0px 5px 1px rgba(255, 174, 0, 0.5);
}
#hero-name-tooltip {
    color: white;
    background: #363245;
    font-family: Reaver-Bold;
    padding-top: 9px;
    font-size: 18px;
    border: 4px solid rgb(22, 22, 27);
    border-radius: 18px;
}
#hero-name-tooltip.react-tooltip__show {
    opacity: 1;
}

#hero-name-tooltip .react-tooltip-arrow {
    width: 18px;
    height: 18px;
    background: #363245;
    border-right: 4px solid rgb(22, 22, 27);
    border-bottom: 4px solid rgb(22, 22, 27);
    border-left: 4px solid #363245;
    border-top: 4px solid #363245;
    mask-image: linear-gradient(
        315deg,
        black 0,
        black 37%,
        transparent 37%,
        transparent 100%
    );
    top: 19px;
}
